  display: flex;
  align-items: top;
  justify-content: center;
  flex-direction: column;
  flex-wrap: wrap;
  font-family: 'Lato', sans-serif;
  color: #1a1a40;
}

.player {
  padding-top: 56.25%;
  position: relative;
  margin: 10px 0;
  border-radius: 4px;
  overflow: hidden;
  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1);
}

iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.brand-title {
  font-family: 'Playfair Display', serif;
  font-size: 1.75rem;
  font-weight: 700;
  color: #1a1a40;
  margin-bottom: 1rem;
  text-align: center;
}

/* Custom UI Elements */
.color-picker-item {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  margin-bottom: 0.5rem;
}

.color-input {
  width: 30px;
  height: 30px;
  border: 1px solid #e5e7eb;
  border-radius: 4px;
  cursor: pointer;
  padding: 0;
  background: none;
}

.color-label {
  font-size: 0.875rem;
  color: #4b5563;
  text-transform: capitalize;
  cursor: pointer;
}

