  background-color: #1a1a40 !important;
  color: white !important;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.05em;
}

.btn-secondary {
  border: 1px solid #1a1a40 !important;
  color: #1a1a40 !important;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.05em;
}

