  width: 800px;
  max-width: 100%;
  margin: 0 auto;
  position: relative;
}

.close-btn {
  position: absolute;
  top: 0;
  right: 0;
  z-index: 10;
}
</style>

<template>
  <div id="editor">
    <!-- control -->
    <div class="editor-content">
      <v-btn icon class="close-btn" @click="$emit('close')">
        <v-icon>mdi-close</v-icon>
      </v-btn>

      <div class="brand-title">
        {{ getTitle() }}
      </div>

      <!-- video -->
      <div class="player">
        <!-- <youtube :video-id="videoId" v-if="videoId != null" /> -->
        <iframe
          width="560"
          height="315"
          :src="videoUrl(videoId, videoTime)"
          title="YouTube video player"
          frameborder="0"
          allow="
            accelerometer;
            autoplay;
            clipboard-write;
            encrypted-media;
            gyroscope;
            picture-in-picture;
            web-share;
          "
          allowfullscreen
        ></iframe>
      </div>

      <!-- paper -->
      <div id="paper" :style="paperSize">
        <component
          :is="currentComponent"
          :roundel="roundel"
          :number="number"
          @openColorInput="openColorInput"
          :roundelScale="roundelScale / 100"
        ></component>
      </div>

      <!-- number field -->
      <div v-if="numberEditable" class="mt-4">
        <v-text-field
          outlined
          dense
          color="#c5a059"
          label="Tail Number (2 digits)"
          placeholder="00"
          v-model="number"
          type="number"
          :rules="[(n) => isNumberValid(n) || 'number is two digits']"
        ></v-text-field>
      </div>

      <div style="display: flex; height: 300px; margin-top: 1rem; gap: 1rem;">
        <!-- color palette -->
        <div
          v-if="currentComponent"
          style="
            display: flex;
            flex-direction: column;
            flex: 1 1;
            height: 100%;
            overflow-y: auto;
            border: 1px solid #e5e7eb;
            border-radius: 4px;
            padding: 1rem;
          "
        >
          <div class="font-weight-bold mb-3" style="color: #1a1a40;">Colors</div>
          <div v-for="[k, v] of Object.entries(colors)" :key="k" class="color-picker-item">
            <input
              class="color-input"
              type="color"
              :name="k"
              :value="v"
              :ref="k"
              :id="k"
              @input="onSelectColor(k, $event)"
            />
            <label :for="k" class="color-label"> {{ k }}</label>
          </div>
        </div>
        <!-- roundel picker -->
        <roundel-picker
          v-if="roundelEditable"
          style="flex: 1 1; border: 1px solid #e5e7eb; border-radius: 4px; padding: 1rem;"
          v-model="roundel"
          :roundelScale="roundelScale"
          @changeRoundelScale="changeRoundelScale"
        />
      </div>

      <!-- print -->
      <div id="printable" v-if="printing" style="display: none;">
        <div v-for="compIndex in +numCopies" :key="compIndex" class="print-page">
          <component
            :is="currentComponent"
            :roundel="roundel"
            :number="number"
            :qrVisible="true"
            :roundelScale="roundelScale / 100"
          ></component>
        </div>
      </div>
      <div
        style="
          display: flex;
          display-orientation: row;
          align-items: center;
          margin-top: 1.5rem;
          gap: 1rem;
        "
      >
        <v-text-field
          style="flex: 1;"
          v-model="numCopies"
          hide-details
          outlined
          dense
          color="#c5a059"
          label="Copies"
          type="number"
          min="1"
        />
        <v-btn class="btn-action" height="40" v-on:click="printOut()"
          >Print</v-btn
        >
        <v-btn class="btn-secondary" height="40" text v-on:click="reset()">Reset</v-btn>
      </div>
    </div>
  </div>
</template>

<script>
// import printJS from "print-js";
import { createFighterComponent } from "@/fighterComponent.js";
import RoundelPicker from "./RoundelPicker.vue";
import { logEvent } from "firebase/analytics";
import { getIdFromURL, getTimeFromURL } from "vue-youtube-embed";
import {
  findFighterDesign,
  getLastFighterDesign,
} from "@/data/fighterDesigns.js";
import version from "@/data/version.js";
import semver from "semver";

export default {
  props: ["analytics", "fighter"],
  components: { RoundelPicker },
  data() {
    return {
      paperSize: "width:100%;max-width:800px;",

      videoId: null,
      videoTime: null,
      design: null,
      currentComponent: null,
      colors: null,
      saveEnabled: false,
      printing: false,
      numCopies: 1,
      numberEditable: false,
      roundelEditable: true,
      number: null,
      roundel: null,
      roundelScale: 100,
    };
  },
  async mounted() {
    console.log("Editor mounted");
    console.log(`Fighter required: ${this.fighter}`);
    this.onChangeFighter(this.fighter);
    console.log(`Fighter design: ${this.design.name}`);
  },
  methods: {
    changeRoundelScale(scale) {
      this.roundelScale = scale;
    },
    getTitle() {
      if (this.design) {
        return this.design.name;
      } else {
        ("");
      }
    },
    onChangeFighter(fighter) {
      this.design = this.getDesign(fighter);
    },
    async reset() {
      this.numCopies = 1;
      localStorage.setItem(this.design.fileName, null);
      await this.load(this.design);
    },
    setVideo(videoId, videoTime) {
      this.videoId = videoId;
      this.videoTime = videoTime;
      console.log(this.videoUrl(this.videoId, this.videoTime));
    },
    videoUrl(videoId, videoTime) {
      return `https://www.youtube.com/embed/${videoId}?t=${videoTime}`;
    },
    getDesign(fighter) {
      return findFighterDesign(fighter) ?? getLastFighterDesign();
    },
    isNumberValid(number) {
      const regex = /^\d{2}$/;
      return regex.test(number);
    },
    enableSave() {
      this.saveEnabled = true;
    },
    disableSave() {
      this.saveEnabled = false;
    },
    async replace(design, colors, roundel, number) {
      console.log("replace");

      console.log("\tcreate fighter component");
      let result = await createFighterComponent(design.fileName);

      for (let key in result.colors) {
        console.log(`[result colors] ${key}`);
      }

      // read colors from template
      this.colors = result.colors;
      if (colors) {
        for (const key in colors) {
          this.colors[key] = colors[key];
        }
      }

      console.log("\tupdate roundel on replace");
      this.roundelEditable =
        design.roundelEditable !== undefined ? design.roundelEditable : true;
      if (roundel) {
        this.roundel = roundel;
      } else {
        this.roundel = design.roundel;
      }

      this.setVideo(
        getIdFromURL(design.videoUrl),
        getTimeFromURL(design.videoUrl),
      );

      console.log("\tupdate number on replace");
      this.numberEditable = design.numberEditable;
      if (number) {
        this.number = number;
      } else {
        this.number = design.number;
      }

      console.log("\tassign fighter component");
      this.currentComponent = result.component;
    },
    save() {
      if (!this.saveEnabled) return;

      console.log("save");

      const editorState = {
        fileName: this.design.fileName,
        roundel: this.roundel,
        colors: this.colors,
        number: this.number,
      };
      localStorage.setItem(this.design.fileName, JSON.stringify(editorState));
      localStorage.setItem("appVersion", version);
      console.log(`Save ${this.design.fileName} with appVersion ${version}`);
    },
    async load(design) {
      console.log("load");
      this.disableSave();

      let editorState = null;
      const lastVersion = localStorage.getItem("appVersion");
      console.log(`Last Version: ${lastVersion}, Current Version: ${version}`);
      if (
        lastVersion &&
        semver.eq(semver.coerce(lastVersion), semver.coerce(version))
      ) {
        const s = localStorage.getItem(design.fileName);
        if (s) {
          try {
            editorState = JSON.parse(s);
          } catch (e) {
            console.log(e);
          }
        }
      }

      if (editorState) {
        await this.replace(
          design,
          editorState.colors,
          editorState.roundel,
          editorState.number,
        );
        console.log(`Load ${editorState.fileName}`);
      } else {
        await this.replace(design);
      }

      this.enableSave();
    },
    onSelectColor(prop, event) {
      console.log(`set prop ${prop} with ${event.target.value}`);
      this.colors[prop] = event.target.value;
      this.save();
    },
    openColorInput(target) {
      console.log(`open color input of ${target}`);
      let colorInput = this.$refs[target][0];
      colorInput.focus();
      colorInput.click();
    },
    printOut() {
      logEvent(this.analytics, "print_triggered");
      logEvent(this.analytics, `print ${this.design.name}`);

      this.printing = true;
      
      this.$nextTick(() => {
        const printable = document.getElementById('printable');
        if (!printable) {
            console.error("Printable element not found");
            this.printing = false;
            return;
        }

        // Clone the printable element
        const clone = printable.cloneNode(true);
        
        // Create a wrapper for the print content
        const printWrapper = document.createElement('div');
        printWrapper.id = 'print-wrapper';
        
        // Apply styles to ensure it fills the page and sits on top
        Object.assign(printWrapper.style, {
            position: 'absolute',
            top: '0',
            left: '0',
            width: '0',   // Hidden on screen
            height: '0',  // Hidden on screen
            overflow: 'hidden', // Hidden on screen
            display: 'block'
        });
        
        // Center the content if desired, similar to original style
        clone.style.margin = 'auto';
        clone.style.display = 'block';
        
        // Ensure that explicit widths on the cloned content don't exceed print width
        // But usually the component inside handles its own size (e.g. SVG).
        // Let's iterate children to ensure they are visible
        
        printWrapper.appendChild(clone);
        document.body.appendChild(printWrapper);

        // Hide the main app content to prevent bleeding
        // We assume the main app is in #app or .v-application. 
        // A safer bet is to hide everything else using a style tag we inject
        const style = document.createElement('style');
        style.id = 'print-style';
        style.textContent = `
            @page {
                size: auto;
                margin: 20mm;
            }
            @media print {
                body > *:not(#print-wrapper) {
                    display: none !important;
                }
                #print-wrapper {
                    width: 100% !important;
                    height: 100% !important;
                    overflow: visible !important;
                    display: block !important;
                    position: absolute !important;
                    top: 0 !important;
                    left: 0 !important;
                    background: white !important;
                }
                .print-page {
                    page-break-after: always;
                    break-after: page;
                    display: block;
                    width: 100%;
                }
                /* Ensure no padding/margin on body interfere */
                body, html {
                    background: white;
                }
            }
        `;
        document.head.appendChild(style);

        // Wait for images/resources in clone (usually quick as they are cached)
        setTimeout(() => {
            window.print();
            
            // Cleanup function
            const cleanup = () => {
                if (document.body.contains(printWrapper)) {
                    document.body.removeChild(printWrapper);
                }
                if (document.head.contains(style)) {
                    document.head.removeChild(style);
                }
                this.printing = false; 
                window.removeEventListener('afterprint', cleanup); 
                // Also remove touch/click listeners if we added them for manual cancel
            };
            
            window.addEventListener('afterprint', cleanup);
            
            // Fallback: If afterprint is flaky (some mobile browsers), 
            // we can also listen for focus (desktop) or just set a timeout?
            // For now, afterprint is the standard way. 
            // We can also allow user to "click" the overlay to close it if print fails to open/close?
            // But overlay covers everything. 
            // Let's rely on AFTERPRINT first.
            
        }, 500);
      });
    },
  },
  watch: {
    fighter: {
      handler(value) {
        this.onChangeFighter(value);
      },
    },
    design: {
      handler(value) {
        this.load(value);
      },
    },
    roundel: {
      handler() {
        this.save();
      },
    },
    number: {
      handler() {
        this.save();
      },
    },
  },
};
</script>
